.about-tour {
  $this: &;
  font-size: 0;
  margin: 110px 0 70px;
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 33.333%;
    padding-bottom: 180px;
    &:nth-child(3n+1) {
      #{$this}__content {
        z-index: 4; } }
    &:nth-child(2n+2) {
      margin-top: -40px;
      #{$this}__content {
        z-index: 3; }
      #{$this}__img {
        img {
          object-fit: cover;
          object-position: top;
          max-height: 360px; } } } }
  &__img img {
    width: 100%; }
  &__content {
    position: absolute;
    bottom: 0;
    text-align: center;
    background: #fff;
    padding: 40px 0;
    max-width: 500px;
    @include border;
    > svg {
      fill: $orange;
      width: 190px;
      height: 8px; } }
  &__desc {
    font-size: 20px;
    padding: 35px 55px;
    @include lora-i; }


  &__item_men,
  &__item_pizza {
    #{$this}__img {
      max-width: 630px; } }

  &__item_men {
    #{$this}__content {
      right: -110px; } }


  &__item_pizza {
    #{$this}__content {
      left: -70px;
      bottom: -20px; } }

  &__item_chief {
    padding-left: 70px;
    padding-right: 70px;
    #{$this}__img {
      max-width: 500px; }
    #{$this}__content {
      right: 40px;
      bottom: -50px; } }
  // &__item_chief
 }  // &__item_pizza
